import React, {Fragment} from 'react'
import {useIntl} from 'react-intl'
import PropTypes from 'prop-types'
import {Controller} from 'react-hook-form'
import {
    FormControl,
    FormErrorMessage,
    FormLabel,
    HStack,
    PinInput,
    PinInputField
} from '@salesforce/retail-react-app/app/components/shared/ui'
import {noop} from '@salesforce/retail-react-app/app/utils/utils'

const PasswordlessTokenFields = ({form, token, pinInputMaxLength, handleTokenChange = noop}) => {
    const {control, formState} = form
    const {formatMessage} = useIntl()
    return (
        <Fragment>
            <Controller
                name="pinInput"
                control={control}
                rules={{
                    required: formatMessage({
                        defaultMessage: 'Please enter your single-use code',
                        id: 'use_passwordless_token_fields.error.required_token'
                    })
                }}
                render={({field}) => {
                    return (
                        <FormControl id="pinInput" isInvalid={formState.errors?.pinInput}>
                            <FormLabel>
                                {formatMessage({
                                    defaultMessage: 'Single-use Code',
                                    id: 'use_passwordless_token_fields.label.token'
                                })}
                            </FormLabel>
                            <HStack>
                                <PinInput
                                    size="md"
                                    variant="filled"
                                    type="number"
                                    otp
                                    {...field}
                                    value={token}
                                    onChange={(value) => {
                                        handleTokenChange(value)
                                        field.onChange(value)
                                    }}
                                    maxLength={pinInputMaxLength}
                                >
                                    {Array.from({length: pinInputMaxLength}).map((_, index) => (
                                        <PinInputField key={index} />
                                    ))}
                                </PinInput>
                            </HStack>
                            {formState.errors?.pinInput && (
                                <FormErrorMessage color="red.600">
                                    {formState.errors.pinInput.message}
                                </FormErrorMessage>
                            )}
                        </FormControl>
                    )
                }}
            />
        </Fragment>
    )
}

PasswordlessTokenFields.propTypes = {
    form: PropTypes.object.isRequired,
    prefix: PropTypes.string,
    token: PropTypes.string,
    pinInputMaxLength: PropTypes.number,
    handleTokenChange: PropTypes.func
}

export default PasswordlessTokenFields
