import React, {Fragment} from 'react'
import PropTypes from 'prop-types'
import {FormattedMessage} from 'react-intl'
import {Alert, Button, Stack, Text} from '@salesforce/retail-react-app/app/components/shared/ui'
import {AlertIcon, BrandLogo} from '@salesforce/retail-react-app/app/components/icons'
import {noop} from '@salesforce/retail-react-app/app/utils/utils'
import PasswordlessTokenFields from '@salesforce/retail-react-app/app/components/forms/passwordless-token-fields'

const PasswordlessTokenForm = ({
    submitForm,
    clickSignIn = noop,
    handleTokenChange = noop,
    form,
    token,
    email,
    pinInputMaxLength = 8,
    isTokenLoading
}) => {
    return (
        <Fragment>
            <Stack justify="center" align="center" spacing={8}>
                <BrandLogo width="60px" height="auto" />
                <Stack spacing={2}>
                    <Text align="center" fontSize="xl" fontWeight="semibold">
                        <FormattedMessage
                            defaultMessage="Check your inbox for your single-use code"
                            id="passwordless_login.title.login_with_email"
                        />
                    </Text>
                    <Text fontSize="sm" align="center" color="gray.700">
                        <FormattedMessage
                            defaultMessage="Lets get you verified. We sent an email to <b>{email}</b>."
                            id="passwordless_login.info.receive_email_shortly"
                            values={{
                                email: email,
                                b: (chunks) => <b>{chunks}</b>
                            }}
                        />
                    </Text>
                </Stack>
            </Stack>
            <form onSubmit={form.handleSubmit(submitForm)}>
                <Stack paddingTop={8} spacing={8} paddingLeft={4} paddingRight={4}>
                    {form.formState.errors?.global && (
                        <Alert status="error">
                            <AlertIcon color="red.500" boxSize={4} />
                            <Text fontSize="sm" ml={3}>
                                {form.formState.errors.global.message}
                            </Text>
                        </Alert>
                    )}
                    <PasswordlessTokenFields
                        form={form}
                        token={token}
                        handleTokenChange={handleTokenChange}
                        pinInputMaxLength={pinInputMaxLength}
                    />
                    <Stack spacing={6}>
                        <Button
                            type="submit"
                            onClick={() => form.clearErrors()}
                            isLoading={isTokenLoading}
                        >
                            <FormattedMessage
                                defaultMessage="Submit code"
                                id="login_form.button.submit_code"
                            />
                        </Button>

                        <Stack direction="row" spacing={1} justify="center">
                            <Text fontSize="sm">
                                <FormattedMessage
                                    defaultMessage="Or return to"
                                    id="passwordless_login_form.message.return_to_sign_in"
                                    description="Precedes link to return to sign in"
                                />
                            </Text>
                            <Button variant="link" size="sm" onClick={clickSignIn}>
                                <FormattedMessage
                                    defaultMessage="Sign In"
                                    id="passwordless_login_form.action.sign_in"
                                />
                            </Button>
                        </Stack>
                    </Stack>
                </Stack>
            </form>
        </Fragment>
    )
}

PasswordlessTokenForm.propTypes = {
    form: PropTypes.object,
    submitForm: PropTypes.func,
    clickSignIn: PropTypes.func,
    handleTokenChange: PropTypes.func,
    email: PropTypes.string,
    token: PropTypes.string,
    pinInputMaxLength: PropTypes.number,
    isTokenLoading: PropTypes.bool
}

export default PasswordlessTokenForm
