import React from 'react'
import PropTypes from 'prop-types'
import {Box} from '@salesforce/retail-react-app/app/components/shared/ui'
import usePasswordlessLoginFields from '@salesforce/retail-react-app/app/components/forms/usePasswordlessLoginFields'
import Field from '@salesforce/retail-react-app/app/components/field'

const PasswordlessLoginFields = ({form, prefix = ''}) => {
    const fields = usePasswordlessLoginFields({form, prefix})

    return (
        <Box>
            <Field {...fields.email} />
        </Box>
    )
}

PasswordlessLoginFields.propTypes = {
    /** Object returned from `useForm` */
    form: PropTypes.object.isRequired,

    /** Optional prefix for field names */
    prefix: PropTypes.string
}

export default PasswordlessLoginFields
