import Auth from '@salesforce/commerce-sdk-react/auth'
import {getConfig} from '@salesforce/pwa-kit-runtime/utils/ssr-config'
import {getAppOrigin} from '@salesforce/pwa-kit-react-sdk/utils/url'
import {API_ERROR_MESSAGE} from '@salesforce/retail-react-app/app/constants'

/**
 * Provides an Auth class used for passwordless authentication
 * uses private client
 */
class PasswordlessAuth extends Auth {
    constructor(config) {
        super(config)
        this.config = config
    }

    handleTokenResponse(res) {
        if (res && res.access_token) {
            const {isGuest} = this.parseSlasJWT(res.access_token)
            this.set('access_token', res.access_token)
            this.set('customer_id', res.customer_id)
            this.set('enc_user_id', res.enc_user_id)
            this.set('expires_in', `${res.expires_in}`)
            this.set('id_token', res.id_token)
            this.set('idp_access_token', res.idp_access_token)
            this.set('token_type', res.token_type)
            this.set('usid', res.usid)
            this.set('customer_type', isGuest ? 'guest' : 'registered')
            const refreshTokenKey = isGuest ? 'refresh_token_guest' : 'refresh_token_registered'
            this.set(refreshTokenKey, res.refresh_token, {
                expires: res.refresh_token_expires_in
            })
        }
    }
}

/**
 * get passwordless login auth
 * @param appOrigin
 * @param app
 * @returns {PasswordlessAuth}
 */
export const getPasswordlessAuth = () => {
    const appOrigin = getAppOrigin()
    const {app} = getConfig()
    return new PasswordlessAuth({
        proxy: `${appOrigin}/${app.commerceAPI.proxyPath.replace(/^\/|\/$/g, '')}`,
        enablePWAKitPrivateClient: true,
        ...app.commerceAPI.parameters
    })
}

/**
 * Authorize passwordless customer
 * @param auth
 * @param email
 * @param siteId
 * @param usid
 * @returns {Promise<{error: null}>}
 */
export const authorizePasswordlessCustomer = async ({email, siteId, usid, accessToken}) => {
    const results = {error: null}
    const {
        app: {commerceAPI}
    } = getConfig()
    const {
        parameters: {organizationId}
    } = commerceAPI
    const appOrigin = getAppOrigin()

    try {
        /**
         * instead auth.client.authorizePasswordlessCustomer, we are using a custom API
         * to ensure the callback url and the secret key are not exposed to the client
         * additionally, this can be extended to handled additional logic
         * for multiple customers being returned for the same email address (IDP scenario)
         */
        const urlParams = new URLSearchParams()
        urlParams.append('siteId', siteId)
        const response = await fetch(
            `${appOrigin}/${commerceAPI.proxyPath.replace(
                /^\/|\/$/g,
                ''
            )}/custom/passwordless-login/v1/organizations/${organizationId}/login?${urlParams.toString()}`,
            {
                method: 'POST',
                headers: {
                    Authorization: `Bearer ${accessToken}`,
                    'Content-Type': 'application/x-www-form-urlencoded'
                },
                body: `c_emailId=${encodeURIComponent(email)}&c_usid=${usid}`
            }
        )
        if (!response.ok) {
            throw new Error(JSON.stringify(API_ERROR_MESSAGE))
        }
        results.data = response.status === 204 ? await response.text() : await response.json()
    } catch (error) {
        results.error = JSON.parse(error.message)
    }
    return results
}

export const getPasswordlessAccessToken = async ({auth, token, shouldMergeBasket, mergeBasket}) => {
    const results = {error: null, res: null}
    try {
        const res = await auth.client.getPasswordLessAccessToken({
            body: {
                pwdless_login_token: token,
                hint: 'pwdless_login',
                grant_type: 'client_credentials'
            },
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            }
        })
        auth.handleTokenResponse(res)
        if (shouldMergeBasket) {
            mergeBasket.mutate({
                headers: {
                    'Content-Type': 'application/json'
                },
                parameters: {
                    createDestinationBasket: true
                }
            })
        }
    } catch (error) {
        results.error = error
    }
    return results
}
