import {useCustomQuery} from '@salesforce/commerce-sdk-react'

const onClient = typeof window !== 'undefined'

/**
 *
 * Use the agents custom API from plugin_qlabs. This filters to just MIAW embedded types
 * and ignores custom miaw and agent API
 *
 * @returns {UseQueryResult<{}|*, unknown>}
 */
export const useMIAWAgents = () => {
    return useCustomQuery(
        {
            options: {
                method: 'GET',
                customApiPathParameters: {
                    apiVersion: 'v1',
                    endpointPath: 'agents',
                    apiName: 'agents'
                }
            }
        },
        {
            select: (data) => {
                return data?.agents?.filter((agent) => agent.type === 'MIAW')
            },
            enabled: onClient
        }
    )
}

/**
 * Retrieves a user verficiation JWT for use with MIAW
 */
export const useMIAWUserVerification = (agent) => {
    return useCustomQuery(
        {
            options: {
                method: 'GET',
                customApiPathParameters: {
                    apiVersion: 'v1',
                    endpointPath: 'verification',
                    apiName: 'agents'
                }
            }
        },
        {
            enabled: agent.userVerification
        }
    )
}
